.data_container {
	display: flex;
	flex-direction: row;
}
main {
	display: flex;
	flex-direction: column;
	margin-top: 2%;
	width: 70%;
	height: 100vh;
	overflow-y: auto;
	padding-bottom: 10%;
}

.card_container {
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
}
.chart {
	display: flex;
	width: 100%;
	margin-top: 5%;
	justify-content: center;
}
.chart_container {
	display: flex;
	width: 100%;
}
section {
	display: flex;
	flex-direction: column;
	width: 30%;
	padding: 2%;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
		rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
	height: 95vh;
	overflow-y: auto;
	padding-bottom: 5%;
}

.clock_container {
	display: flex;
	justify-content: center;
}

.activity_container {
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: center;
	padding-top: 10%;
	padding-bottom: 2%;
}

.activity {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.activity_container h5 {
	font-size: 0.9rem;
	margin-top: 4%;
	margin-bottom: 0.6rem;
}

.react-calender {
	border: none;
	font-size: 18px;
	font-family: "Poppins", sans-serif;
}

@media screen and (max-width: 700px) {
	.data_container {
		display: flex;
		flex-direction: column;
	}
	main {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-top: 4%;
		margin-bottom: 2%;
		width: 100%;
		order: 2;
	}
	.card_container {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
	}
	.chart {
		display: flex;
		width: 100%;
		max-height: 40vh;
		justify-content: center;
	}
	.chart_container {
		display: flex;
		width: 90%;
	}
	section {
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 1%;
		height: auto;
		margin-top: 2%;
		order: 1;
		box-shadow: 0px 4px 60px 0px #e5e5e5;
	}
}
